import React from "react";
import { Outlet } from "react-router-dom";
import { StrapiProvider } from "react-strapi";
import axios from "axios";
import { Footer, Header } from "./components";
import { PreferenceResponse } from "./schemas";
import "./App.scss";

export type AppLoaderData = {
  preferences: PreferenceResponse;
};

export async function appLoader(): Promise<AppLoaderData> {
  try {
    const response = await axios.get<PreferenceResponse>(
      "/preference?populate=*"
    );
    return {
      preferences: response.data,
    };
  } catch (error) {
    throw error;
  }
}

function App() {
  return (
    <StrapiProvider serverUri="https://strapi.coulsdonpartnership.org" rememberMe>
      <Header />
      <Outlet />
      <Footer />
    </StrapiProvider>
  );
}

export default App;
